@include tablet{
    .contenedor-anuncios{
        @include grid(3,1fr);
        gap: 2rem;
    }
}
.anuncio{
    background-color: lighten($gris, 5);
    border: 3px solid $gris;
    border-radius: 5px;
    margin-bottom: 3rem;
    .contenido-anuncio{
        padding: 2rem;
        h3,p{
            margin: 0;

        }
        .precio{
            color: $verde;
            font-weight: $bold;
            font-size: 2.4rem;
            text-align: center;
        }
    }
    .img{
      height: 25rem; 
    }
}
.iconos-caracteristicas{
    display: flex;
    list-style: none;
    padding: 0;
    li{
        flex: 1;
        display: flex;
        justify-content: center;
        align-items: center;
        img{
            flex:0 0 4rem;
            margin-right: 1rem;
            height: 2rem ;
            

        }
        p{
            font-weight: $black;
        }
    }
}
.precio{
    color: $verde;
    font-weight: $bold;
    font-size: 2.4rem;
    text-align: center;
}

