.derecha{
display: flex;
flex-direction: column;
align-items: center;
@include tablet{
    align-items: flex-end;
    margin: 2rem 0 0 0;
}
.dark-mode-boton{
    width: 3rem;
    filter: invert(100%);
}
}

.barra{
    display: flex;
    padding-top: 3rem;
    flex-direction: column;
    
    @include tablet{
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-end;
    }
}
.navegacion{
    visibility: visible;
    opacity: 0;
    height: 0;
    transition: visibility .1s ease-in-out,
                opacity .1s linear,
                height 1s step-end ;


    @include tablet{
        display: flex;
        align-items: center;
        visibility: visible;
        opacity: 1;
    }
    a{
        &:hover{
           cursor: pointer;
            color: $verde;
           
        }
        color: $blanco;
        text-decoration: none;
        font-size: 2.2rem;
        display: block;
        text-align: center;
        @include tablet{
            visibility: visible;
            margin-right: 2rem;
            &:last-of-type{
                margin-right: 0;
            }
        }
    }
}
img{
    height: 60px;
    &:hover{
        cursor: pointer;
    }
}

@include tablet{
    .mobil-menu{
        display: none;

    }
}
.mostrar{
   visibility: visible;
   opacity: 1;
   height: auto;
}