.alerta{
    padding: .5rem;
    text-align: center;
    color: $blanco;
    margin: 1rem auto;
    text-transform:uppercase ;
    max-width: 50rem;
    border-radius: 10px;
    border: 2px solid $negro;
    
    &.error{
        background-color: $rojo;
    }
    &.exito{
        background-color: $verde;
    }
}
table.propiedades{
    margin-top: 4rem;
    width: 100%;
    border-spacing: 0;

    th{
        color: $blanco;
        padding: 2rem;
    }
    thead{
        background-color: $verde;
    }
    .imagen-tabla{
        width: 10rem;
    }
}
.imagen-actualizar{
    height: 20rem;
    width: 20rem;
}