.nosotros{
   @include tablet{
    @include grid(2,4rem);
    img{
        height: 100%;
    }

   }
   img{
       height: 100%;
   }
   margin-bottom: 5rem;

}
.separacion-nosotros{
    margin: $separacion 0;

}
.texto-nosotros blockquote{
    font-weight: $black;
    font-size: 2.3rem;
    margin: 3rem 0 0 0;
    padding-left: 1rem 0 3rem;

    @include tablet{
        margin: 0;
    }
}