.contenedor-formulario{
    border: 2px solid $grisOscuro;
    padding: 3rem;
    border-radius: 10px;
    -webkit-box-shadow: 0px 0px 10px 3px $grisOscuro;
-moz-box-shadow: 0px 0px 10px 3px $grisOscuro;
box-shadow: 0px 0px 10px 3px $grisOscuro;
}


.formulario{
     
    p{
        font-size: 1.9rem;
        color: $grisOscuro;
        margin: 2rem 0 0 0;
        text-transform: uppercase;
    }
    legend{
        font-size: 2rem;
        color: $grisOscuro;
        border-radius: 10px;
        border: 1px solid $grisOscuro;
        margin-top: 1rem;
        padding: .5rem;
    }
    label{
        font-weight: bold;
        text-transform: uppercase;
        display: block;
    }
    input:not([type="submit"]),textarea,select{
        padding: 1rem;
        display: block;
        width: 80%;
        background-color: $blanco;
        border: 1px solid $gris;
        border-radius: 10px;
        margin-bottom: 2rem;
    }
    .forma-contacto{
        max-width: 30rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    input[type="radio"]{
        margin: 0;
        width: auto;
    }
    textarea{
        height: 20rem;
    }
    fieldset{
        border-radius: 5px ;
        border: 1px solid $grisOscuro
    }
    .legend-centrado{
        text-align: center;
    }
  
}