.dark-mode{
    background-color: darken($grisOscuro, 10);
    p{
        color: $blanco;
    }
    h1,h2,h3,h4{
        color: $gris;
    }
    .anuncio{
        background-color: $grisOscuro;
        border: 1px solid $grisOscuro;
        
    }
    .icono-caracteristicas{
        filter: invert(100%);
    }
    .precio{
        color: $verde;
    }
    .texto-nosotros blockquote{
        color: $blanco;
    }
    .formulario{
        label,legend,p{
            color: $gris;
        }
        input:not([type="submit"]),textarea,select{
           background-color: $grisOscuro;
           color: $blanco;
           border: 1px solid $grisOscuro
        }
        legend{
            border-radius: 10px;
            border: 1px solid $grisOscuro;
            margin-top: 1rem;
            padding: .5rem;
        }
    }
    fieldset{
        border-radius: 5px ;
        border: 1px solid $grisOscuro
    }
    
}